import { BrowserModule } from '@angular/platform-browser';
import { APP_INITIALIZER, LOCALE_ID, NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { CollaborateurComponent } from './admin-user-front/pages/collaborateur/collaborateur.component';
import { FormsModule } from '@angular/forms';
import { AdminDatatableComponent } from './comps/admin-datatable/admin-datatable.component';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NavbarComponent } from './navbar/navbar.component';
import { NvneBtnComponent } from './comps/nvne-btn/nvne-btn.component';
import { NvneBtnOComponent } from './comps/nvne-btn-o/nvne-btn-o.component';
import { ToastsContainer } from './toasts-container.component';
import { KeycloakSecurityService } from './services/security/keycloak-security.service';
import { RequestInterceptorService } from './services/security/request-interceptor.service';
import { EntrepriseComponent } from './admin-user-front/pages/entreprise/entreprise.component';
import {LocationStrategy, HashLocationStrategy, registerLocaleData} from '@angular/common';
import localeFr from '@angular/common/locales/fr';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NgbdSortableHeader } from './comps/admin-datatable/admin-datatable.component';
registerLocaleData(localeFr);

export function kcFactory(kcSecurity:KeycloakSecurityService){
  return ()=> kcSecurity.init();
}

@NgModule({
  declarations: [
    AppComponent,
    ToastsContainer,
    CollaborateurComponent,
    AdminDatatableComponent,
    NavbarComponent,
    NvneBtnComponent,
    NvneBtnOComponent,
    EntrepriseComponent,
    NgbdSortableHeader
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    HttpClientModule,
    NgbModule
  ],
  providers: [
    {provide:APP_INITIALIZER,deps:[KeycloakSecurityService],useFactory:kcFactory,multi:true},
    {provide:HTTP_INTERCEPTORS, useClass:RequestInterceptorService, multi:true},
    {provide: LocationStrategy, useClass: HashLocationStrategy},
    { provide: LOCALE_ID, useValue: 'fr-FR'}
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
