import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CollaborateurComponent } from './admin-user-front/pages/collaborateur/collaborateur.component';
import { EntrepriseComponent } from './admin-user-front/pages/entreprise/entreprise.component';

const routes: Routes = [
  { path: 'collaborateurs', component: CollaborateurComponent },
  { path: 'entreprises', component: EntrepriseComponent },
  { path: '', component: CollaborateurComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
