import { Component, OnInit, Input, Output, EventEmitter, ViewChildren, QueryList, Directive } from '@angular/core';
import { DonneesService } from 'src/app/services/donnees.service';
import { ToastService } from 'src/app/services/toast.service';

// export type SortColumn = keyof this.data | '';
export type SortDirection = 'asc' | 'desc' | '';
const rotate: {[key: string]: SortDirection} = { 'asc': 'desc', 'desc': '', '': 'asc' };

const compare = (v1: string | number, v2: string | number) => v1 < v2 ? -1 : v1 > v2 ? 1 : 0;

export interface SortEvent {
  column: string;
  direction: SortDirection;
}

@Directive({
  selector: 'th[sortable]',
  host: {
    '[class.asc]': 'direction === "asc"',
    '[class.desc]': 'direction === "desc"',
    '(click)': 'rotate()'
  }
})
  
export class NgbdSortableHeader {

  @Input() sortable: string = '';
  @Input() direction: SortDirection = '';
  @Output() sort = new EventEmitter<SortEvent>();

  rotate() {
    this.direction = rotate[this.direction];
    this.sort.emit({column: this.sortable, direction: this.direction});
  }
}

@Component({
  selector: 'app-admin-datatable',
  templateUrl: './admin-datatable.component.html',
  styleUrls: ['./admin-datatable.component.css']
})

export class AdminDatatableComponent implements OnInit {

  public data:any

  @Input() public size:number=5;
  public currentPage:number=0;
  public totalPages:number=0;
  public totalElements:number=0;
  public pages:Array<number>;
  public from:number=0;
  public to:number=0;
  @Input() public resourceUrlPath:string;
  @Input() public columnDefs:any;
  @Input() public resourceName:string;
  @Input() public title:string

  constructor(private donneesService:DonneesService,public toastService: ToastService) { }

  ngOnInit(): void {
    this.onGetResource();
  }

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;

  onSort({column, direction}: SortEvent) {
    console.log("sorting...");

    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting countries
    if (direction === '' || column === '') {
      // this.data = this.data; 
    } else {
      this.data = [...this.data].sort((a, b) => {
        const res = compare(a[column], b[column]);
        return direction === 'asc' ? res : -res;
      });
    }
  }

  private handleData(data){
    this.totalPages=data["page"].totalPages;
    this.totalElements=data["page"].totalElements;
    this.pages=new Array<number>(this.totalPages);
    this.data=data._embedded[this.resourceName];
    this.from=(this.currentPage*this.size)+1;
    this.to=+(this.currentPage*this.size) + +this.size;
    if (this.to > this.totalElements) this.to = this.totalElements;
    console.log(this.data);
  }

  onGetResource(){
    this.donneesService.getResource(this.currentPage,this.size,this.resourceUrlPath)
      .subscribe(data=>{
      this.handleData(data);
    }, err=>{
      console.log(err);
    })
  }

  contactSwitch(data:any, index:string, header:string){
    console.log("switching "+index+" to "+data[index]);
    this.donneesService.contactSwitch(data,this.resourceUrlPath).subscribe(data=>{
      console.log(data);
      let msg = data[index]? header+" activé" : header+" desactivé";
      this.toastService.show(msg, { classname: 'bg-success text-light', delay: 3000 });
      // this.result = data;
      // this.errormsg="";
    }, err=>{
      this.toastService.show("Echec", { classname: 'bg-danger text-light', delay: 3000 });
      console.log(JSON.parse(err._body).message);
    });
  }

  onChangeNumEntries(value){
    console.log(value);
    this.size = value.size;
    this.onGetResource();
  }

  onPageDossier(i){
    this.currentPage=i;
    this.onGetResource();
  }

  onChercher(value: any){
    console.log(value)
    this.donneesService.getResourceByKeyword(this.currentPage,this.size,this.resourceUrlPath)
      .subscribe(data=>{
        console.log(data)
      this.handleData(data);
    }, err=>{
      console.log(err);
    })
  }

  onDeleteProduct(p){
    let conf=confirm("Êtes-vous sûre ?");
    if (conf) {
      this.donneesService.deleteResource(p._links.self.href)
        .subscribe(data=>{
          this.onGetResource();
        }, err=>{
          console.log(err);
        })
    }
  }

}
