import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {KeycloakInstance} from "keycloak-js";
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

declare var Keycloak: any;

@Injectable({
  providedIn: 'root'
})

export class KeycloakSecurityService {
  public kc:KeycloakInstance;
  public realm;
  public clientId;
  public kcUrl;

  constructor(private router:Router, private httpClient:HttpClient) { }

  async init(){
    console.log("Security Information ... "+environment.authApiUrl);
    console.log("Contacting "+environment.authApiUrl+"/");
    this.realm = "nvne-realm";
    // this.clientId = "nvne-user-admin-front";
    this.clientId = "userApi";
    // this.kcUrl = "localhost:8080/auth/";
    this.kcUrl = environment.authApiUrl+"/";
    this.kc= new Keycloak({
      url:this.kcUrl,
      realm:this.realm,
      clientId:this.clientId
    });
    await this.kc.init({
      onLoad:'login-required',
    //   onLoad:'check-sso',
      promiseType:'native'
    });
    if (!this.kc.tokenParsed.realm_access.roles.includes("admin")) {
      this.kc.logout();
    };
    console.log(this.kc.token);
  }

}
